























































































import {
  defineComponent,
  ref,
  onMounted,
  computed,
  reactive,
  toRefs
} from '@vue/composition-api';
import { fbStart } from '@/auth';
import {
  emailField,
  standardField,
  requiredField,
  shortPassword,
  tooLongField
} from '@/use/validations';

export default defineComponent({
  name: 'TheLoginForm',
  props: {
    formProps: {
      type: Object,
      required: true
    }
  },
  setup(props, { root: { $store } }) {
    const formData = reactive({
      email: '',
      password: '',
      username: '',
      confirmPassword: ''
    });
    const showPassword = ref(false);
    const valid = ref(false);
    const EmailRules = [requiredField, emailField, tooLongField];
    const passwordRules = [...standardField, requiredField, shortPassword];

    const comparePasswords = computed(() => {
      return (
        formData.password === formData.confirmPassword ||
        'Passwords do not match'
      );
    });

    const onSubmit = () => {
      if (props.formProps.signUp) {
        $store.dispatch('user/signUpUser', formData);
      } else {
        $store.dispatch('user/signInUser', formData);
      }
    };

    onMounted(() => {
      fbStart();
    });

    return {
      ...toRefs(formData),
      showPassword,
      valid,
      onSubmit,
      EmailRules,
      passwordRules,
      comparePasswords
    };
  }
});
